<template>
  <div id="app">
    <v-flash></v-flash>
  </div>
</template>

<script>
import vFlash from './components/flash'

export default {
  name: 'app',
  components: {
    vFlash
  }
}
</script>

<style lang="scss">
@import "~@/assets/scss/base.scss";
html,body{
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
#app{
  width: 100%;
  height: 100%;
}
</style>



